html{
  width: 100%;
  height: 100%;
}
html, body{
  font-family: 'Roboto', sans-serif;
  padding: 0px;
  margin: 0px;
  --background: #fff;
  --primary-color: #212425;
  --border-color: rgba(0,0,0,.13);
  --link-color: #1a73e8;
  --z-depth-1: 0 2px 2px rgba(0,0,0,0.22);
  --inputs-bg: #e5e5e5;
  background: var(--background);
  color: var(--primary-color);
  min-height: 100%;
  

}
img{
  max-width: 100%;
}
.select-css {
  display: block;
  font-size: 13px;
  font-family: inherit;
  font-weight: 400;
  color: var(--primary-color);
  line-height: nornmal;
  padding: 6px 8px;
  padding-right: 21px;
  width: 100%;
  max-width: 100%; 
  box-sizing: border-box;
  margin: 0;
  border: none;
  box-shadow: none;
  border-radius: 4px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: var(--inputs-bg);
  background-image: url('./images/arrow_down.svg');
  background-repeat: no-repeat, repeat;
  background-position: right .7em top 50%, 0 0;
  background-size: 10px;
}
.select-css::-ms-expand {
  display: none;
}
.select-css:focus {
  box-shadow: 0 0 1px 1px #9bc6ff;
  outline: none;
}
.select-css option {
  background-color: #fff;
  color: black;
  font-weight: normal;
}
.search-css {
  font-size: 13px;
  border: none;
  background-color: var(--inputs-bg);
  color: var(--primary-color);
  border-radius: 4px;
  padding: 6px 8px;  
  box-sizing: border-box;
}
.search-css:focus {
  outline: none;
  box-shadow: 0 0 1px 1px #9bc6ff;
}

.news{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem;
  max-width: 600px;
  margin: 0 auto;
}
.newsModule{
  padding: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 25px;
}
.newsModule:hover{
  box-shadow: var(--z-depth-1);
  transition: box-shadow 200ms ease;
}
.newsModule__image{
  border-radius: 5px;
  max-width: 145px;
  max-height: 100px;
  width: 100%;
  margin-left: auto;
}
.newsModule__text {
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
}
.newsModule__text--title {
  font-weight: 500;
  margin: 0;
}
.newsModule__text--link{
  font-weight: 500;
  text-decoration: none;
  color: var(--link-color);
}
.newsModule__text--link::before{
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
.newsForm {
  margin-bottom: 1rem;
  border: 1px solid var(--border-color);
  width: 100%;
  border-radius: 6px;
  padding: 1rem;
}
.newsForm__title{
  margin: 0;
  font-weight: 500;
}
.newsForm__form{
  display: flex;
}
.newsForm__select{
  display: flex;
  margin: .5rem 1rem;
  margin-left: 0;
}
.newsForm__select:last-child{
  margin-right: 0;
}
.switchTheme {
  padding-bottom: 1rem;
  padding-left: 1rem;
}
.switchTheme__img{
  display: block;
  cursor: pointer;
  width: 24px;
  height: 24px;
  display: block;
  z-index: 2;
}
.switchTheme__img--night{
  filter: invert(1)
}
@media(max-width: 530px) {
  .newsForm__form{
    flex-wrap: wrap;
  }
}
@media(max-width: 601px) {
  .newsModule{
    flex-direction: column-reverse;
    align-items: flex-start;     
  }
  .newsModule__image{
    margin: 0 0 .5rem 0;
    max-width: 100%;
    max-height: 100%;  
  }
  .newsModule__text--link::before{
    content: none;
  }
  .newsModule__text--link{
    text-align: right
  }
}
